import React, { useEffect } from 'react'
import './style.css'

import TaskScreen from 'components/TaskScreen'
import CluesScreen from 'components/CluesScreen'
import LoadingScreen from 'components/LoadingScreen'
import SubLevelScreen from 'components/SubLevelScreen'
import StartScreen from 'components/StartScreen'
import TaskListScreen from 'components/TaskListScreen'
import CompletionScreen from 'components/CompletionScreen'


import { useActiveScreen } from '../../store/ActiveScreen';
import { useAuth } from '../../store/authStore'

import { useHistory, useLocation } from 'react-router-dom';
import { useParams } from 'react-router-dom';
import QRscanner from 'components/QRscanner'
import { userActiveLevelDetailsStore } from '../../store/ActiveLevelDetailsStore'


const GameScreen = () => {

  const { ActiveScreen, setActiveScreen } = useActiveScreen();
  const { auth } = useAuth();

  const history = useHistory();
  const params = useParams();

  const search = useLocation().search;

  const {   loadActiveleveldetailsbyqr } = userActiveLevelDetailsStore();
  useEffect(() => {
    if (!params.instanceId) {
      history.replace("/4/")
    }
    else if (String(params.instanceId) === "4") {
      setTimeout(() => {
        if (!auth)
          setActiveScreen("StartScreen")
        else
          setActiveScreen("StartScreen/ActiveQR")
      }, 3000);
    } else {
      setTimeout(() => {
        if (!auth)
          setActiveScreen("StartScreen")
        else {

          const id = new URLSearchParams(search).get('id');
          if (id) {
            console.log("======>>>", id)
            loadActiveleveldetailsbyqr(id)
            setActiveScreen("TaskScreen")
          }
          else {
            setActiveScreen("TaskListScreen")
          }
        }


      }, 3000);
    }
  }, [])




  return (
    <>
      {ActiveScreen === "LoadingScreen" &&
        <LoadingScreen />
      }
      {ActiveScreen === "LoadingScreen/ActiveQR" &&
        <>
          <LoadingScreen />
          <QRscanner />
        </>
      }
      {ActiveScreen === "StartScreen" &&
        <StartScreen QRreader={false} />
      }
      {ActiveScreen === "StartScreen/ActiveQR" &&
        <>
          <StartScreen />
          <QRscanner />
        </>
      }
      {ActiveScreen === "TaskScreen/ActiveQR" &&
        <>
          <TaskScreen />
          <QRscanner />
        </>

      }
      {ActiveScreen === "CluesScreen/ActiveQR" &&
        <>
          <CluesScreen />
          <QRscanner />
        </>

      }

      {ActiveScreen === "SubLevelScreen/ActiveQR" &&
        <>
          <SubLevelScreen />
          <QRscanner />
        </>

      }
      {ActiveScreen === "TaskListScreen/ActiveQR" &&
        <>
          <TaskListScreen />
          <QRscanner />
        </>

      }

      {ActiveScreen === "CompletionScreen/ActiveQR" &&
        <>
          <CompletionScreen />
          <QRscanner />
        </>

      }


      {ActiveScreen === "TaskScreen" &&
        <TaskScreen />
      }
      {ActiveScreen === "CluesScreen" &&
        <CluesScreen />
      }

      {ActiveScreen === "SubLevelScreen" &&
        <SubLevelScreen />
      }
      {ActiveScreen === "TaskListScreen" &&
        <TaskListScreen />
      }

      {ActiveScreen === "CompletionScreen" &&
        <CompletionScreen />
      }
    </>
  )
}
export default GameScreen;