import React, { useEffect, useState } from 'react'
import './style.css'
import * as backimgtop from '../../assets/Clean Walk/Levels_Screen/BG_top.svg';
import { useParams } from 'react-router-dom';
import { useHistory } from 'react-router-dom';
import * as menu from '../../assets/Clean Walk/Video/VID_next.png'
import * as close from '../../assets/Clean Walk/Video/VID_next.png'
import useCountDown from 'react-countdown-hook';
import Slider from '@material-ui/core/Slider/Slider';
import Spinner from 'components/Common/Spinner';
import { fileupload } from 'utils/actions';
import HeaderComponent from 'components/Common/HeaderComponent';
import Camera, { FACING_MODES } from 'react-html5-camera-photo';
import 'react-html5-camera-photo/build/css/index.css';
import * as switchcamera from '../../assets/Clean Walk/rotate.svg'
import * as camer from '../../assets/Clean Walk/camer_icon.png'
import { ImagePreview } from 'components/Common/ImagePreview';
import AudioRecord from 'components/Common/AudioRecord';

const ActivityScreen = (props: any) => {

  const [camera, setcamera] = useState(false)
  const [popup, setpopup] = useState(false)

  const [share, setshare] = useState(false)

  const [timeLeft, { start, pause, resume, reset }] = useCountDown(60 * 1000, 1000);  
  const [timerglow, settimerglow] = useState(false)
  const [submitted, setsubmitted] = useState(false)
  const [inputvalue, setinputvalue] = useState("")
  const [inputvalue1, setinputvalue1] = useState(0)
  const [spinner, setspinner] = useState(true)

  const [idealFacingMode, setIdealFacingMode] = useState(false);
  const [isMaxResolution, setIsMaxResolution] = useState(false);
  const [dataUri, setDataUri] = useState('');

  const _handleTakePhotoAnimationDone = (data: any) => {

    var byteString;
    if (data.split(',')[0].indexOf('base64') >= 0)
      byteString = atob(data.split(',')[1]);
    else
      byteString = unescape(data.split(',')[1]);

    // separate out the mime component
    var mimeString = data.split(',')[0].split(':')[1].split(';')[0];

    // write the bytes of the string to a typed array
    var ia = new Uint8Array(byteString.length);
    for (var i = 0; i < byteString.length; i++) {
      ia[i] = byteString.charCodeAt(i);
    }
    console.log('takePhoto');
    let formdata = new FormData()
    formdata.append("file", new Blob([ia], { type: mimeString }))

    // fileupload(Callback ,formdata)


    console.log(formdata);
  }


  const [submitactivate, setsubmitactivate] = useState(false)
  let params = useParams();

  const history = useHistory();


  const Callback = async (data: any, errorresponse: any) => {
    if (data.status === 200) {
      console.log("Log in respnse :", data.data.result.file_url)
      setinputvalue(data.data.result.file_url)
      setDataUri(data.data.result.file_url)
    }
    else {
      console.log('error ' + JSON.stringify(data));
      console.log('error ' + JSON.stringify(errorresponse));
    }
  };

  useEffect(() => {

  }, [])

  const marks = [
    {
      value: 0,
      label: 'Unpleasant',
    },
    {
      value: 50,
      label: 'Neutral',
    },
    {
      value: 100,
      label: 'Pleasant',
    },
  ];

  return (

    <div style={{ backgroundColor: "#f1fdfd" }}>
      {/* {spinner ?
        <div style={{
          width: '100vw',
          height: '100vh',
          position: 'absolute',
          top: '0',
          zIndex: 1000,
          backgroundColor: '#00243a',
        }}>
          <Spinner />
        </div> :
        null} */}
      <span>
        <img className='bgimgtop'
          src={backimgtop}
        />
      </span>

      <div className="activity_bannermenu" onClick={() => {


      }}>
        <img className='activity_menuicon' src={menu} />
      </div>


      <HeaderComponent />

      {(props.sl_misc === "TIMER") ?
        <div className="activity_main_container">
          <div className="activity_container">
            <div className='PageTitle_OrangeU'>{props.sl_title}</div>
            <div className='pageDescription_Text'>{props.sl_body}</div>
          </div>

          <div className="activity_actioncontainer">

            <div className="activity_timercontainer">

              <div className="activity_timercontainer1">
                {timerglow ?
                  <div>
                    <div className='activity_timer1'>TIMER</div>
                    <div className='activity_starttimer'>{timeLeft / 1000}</div>
                  </div>
                  :
                  <div>
                    <div className='activity_timer1'>TIMER</div>
                    <div className='activity_starttimer'>{timeLeft / 1000}</div>
                  </div>
                }

              </div>

              <div>
                {timerglow ?
                  <button onClick={() => {
                    start()
                    settimerglow(!timerglow)
                  }} className="activity_buttonstart1">
                    <div className="activity_buttontext1">
                      START/RETRY
                    </div>
                  </button>
                  :
                  <button onClick={() => {
                    start()
                    settimerglow(!timerglow)
                  }} className="activity_buttonstart_false1">
                    <div className="activity_buttontext1">
                      START/RETRY
                    </div>
                  </button>}
              </div>

              <div className="activity_timercontainer3_1">
                <div className="activity_timercontainer3_overlap1">
                  <div className='activity_result1'>Your Result </div>
                  <input id="activity_input_value" placeholder={inputvalue} className='activity_input_timer1' type="Number" />
                </div>
              </div>

            </div>

            <div className="button_container">
              {submitted ?
                <button className="continue_button_Green" style={{ marginTop: "-5%" }} onClick={() => {
                  console.log(document.getElementById('activity_input_value'))

                }}>
                  <div onClick={() => {

                  }} className="button_text">Continue</div>
                </button>
                :
                <button className="continue_button_Orange" style={{ marginTop: "-5%" }} >
                  <div className="button_text">SUBMIT</div>
                </button>
              }
            </div>
          </div>

        </div>
        :
        null
      }

      {(props.sl_misc === "INDICATOR") ?
        <div className="activity_main_container">
          <div className='PageTitle_OrangeU'>{props.sl_title}</div>
          <div className="activity_container">
            <div className='pageDescription_Text'>{props.sl_body}</div>
            <div className="activity_actioncontainer">
              <div className="activity_slider_container">
                <div className="activity_slider">
                  <div className='PageTitle_OrangeU' style={{ margin: "-2vh 0 6vh", textDecoration: "none" }}>{inputvalue1}%</div>
                  <Slider
                    step={1}
                    min={0}
                    max={100}
                    valueLabelDisplay="auto"
                    value={inputvalue1}
                    onChange={(data, value) => {
                      console.log(value, data)
                      setinputvalue1(value)
                    }}
                  />

                </div>
                {submitactivate ?
                  <div className="button_text">Correct answer: 28%</div>
                  : null
                }
              </div>
              <div className="button_container">
                {submitted ?
                  <button className="continue_button_Green" onClick={() => {
                    console.log(document.getElementById('activity_input_value'))

                  }}>
                    <div onClick={() => { }} className="button_text">Continue</div>
                  </button>
                  :
                  <button className="continue_button_Orange"
                  >
                    <div className="button_text">SUBMIT</div>
                  </button>
                }
              </div>
            </div>
          </div>
        </div>
        :
        null
      }

      {(props.sl_misc === "FILE UPLOAD") ?
        <div className="activity_main_container">
          <div className="activity_container">
            <div className='PageTitle_OrangeU'>{props.sl_title}</div>
            <div className='pageDescription_Text'>{props.sl_body}</div>
          </div>

          <div className="activity_actioncontainer">
            <label>
            </label>

            <div className="upload-wrap">
              <button type="button" className="nice-button">upload_file</button>
              <input type="file" name="file" className="upload-btn" id="activity_input_value"
              //  onChange={_onChangeHandler} 
              />
            </div>
            {
              (inputvalue !== null) ? <div>
                <img
                  className='activity_selectedimage' src={inputvalue} />
              </div> : null
            }
            <div className="button_container">
              {submitted ?
                <button className="continue_button_Green" onClick={() => {
                  // history.replace('/TaskScreen/' + params.id)
                }}>
                  <div
                    // onClick={() => { history.replace('/TaskScreen/' + params.id) }}
                    className="button_text">Continue</div>
                </button>
                :
                <button className="continue_button_Orange"
                // onClick={() => {
                //   updateLSD(params.id)
                //   setsubmitted(true)
                //   setpopup(true)
                // }}
                >
                  <div className="button_text">SUBMIT</div>
                </button>
              }
            </div>
          </div>
        </div>
        :
        null
      }

      {(props.sl_misc === "CAMERA") ?
        <div className="activity_main_container">
          <div className="activity_container">
            <div className='PageTitle_OrangeU'>{props.sl_title}</div>
            <div className='pageDescription_Text'>{props.sl_body}</div>
          </div>

          <div className="activity_actioncontainer">
            <img onClick={() => {
              setcamera(true)
            }}
              className='activity_instaicon4' src={camer} />

            <div>
              <ImagePreview dataUri={dataUri} />
            </div>

            {share ? <div className="activity_share_fb4">

              <div className='activity__fb2main4'>
                <img onClick={() => {
                  if (window.confirm('Strike a superhero pose and upload it with facebook')) {
                    window.location.href = 'https://www.facebook.com/sharer.php?u=' + String(dataUri);
                  };
                }}
                  className='activity_instaicon4' src={facebook} />
              </div>
              <div className='activity__fb2main4'>
                <img onClick={() => {
                  if (window.confirm('Strike a superhero pose and upload it with twitter')) {
                    window.location.href = 'http://www.twitter.com/share?text=thesearch.bengalurusustainabilityforum.org&url=' + String(dataUri);
                  };
                }}
                  className='activity_instaicon4' src={twitter} />
                {/* <TwitterShareButton className='fbicon'
                      url={"https://trtappfiles.s3.ap-south-1.amazonaws.com/Eco_CS+Files/LEVELS/LS_lev_01_on.png"}>
                      <TwitterIcon size={'16vw'} round={true} />
                    </TwitterShareButton> */}
              </div>

            </div>
              : null}
            <div className="button_container">
              {submitted ?
                <button className="continue_button_Green"
                  onClick={() => {
                    // console.log(document.getElementById('activity_input_value'))
                  }}>
                  <div onClick={() => {
                    //  history.replace('/TaskScreen/' + params.id) 
                  }}
                    className="button_text">Continue</div>
                </button>
                :
                <button className="continue_button_Orange" onClick={() => {
                  // updateLSD(params.id)
                  // setsubmitted(true)
                  // saveDatatoLS("done")
                }}>
                  <div className="button_text">SUBMIT</div>
                </button>
              }
            </div>
          </div>
        </div>
        :
        null
      }

      {(props.sl_misc === "TEXTBOX") ?
        <div className="activity_main_container">

          <div className="activity_container">
            <div className='PageTitle_OrangeU'>{props.sl_title}</div>
            <div className='pageDescription_Text'>{props.sl_body}</div>
          </div>

          <div className="activity_actioncontainer">
            <div className="activity_inputcontainer8">
              <input type="text" id="activity_input_value" placeholder={inputvalue} className='activity_input8' />
            </div>
            <div className="button_container">
              {submitted ?
                <button className="continue_button_Green" onClick={() => {
                  // console.log(document.getElementById('activity_input_value'))
                }}>
                  <div onClick={() => {
                    //  history.replace('/TaskScreen/' + params.id)
                  }}
                    className="button_text">Continue</div>
                </button>
                :
                <button className="continue_button_Orange" onClick={() => {
                  // let buff = document.getElementById('activity_input_value').value
                  // if (buff !== "") {
                  //   updateLSD(params.id)
                  //   setsubmitted(true)
                  //   saveDatatoLS(buff)
                  // }
                }}>
                  <div className="button_text">SUBMIT</div>
                </button>
              }
            </div>
          </div>
        </div>
        :
        null
      }

      {(props.sl_misc === "VOICE RECORDER") ?
        <div className="activity_main_container">
          <div className="activity_container">
            <div className='PageTitle_OrangeU'>{props.sl_title}</div>
            <div className='pageDescription_Text'>{props.sl_body}</div>
          </div>

          <div className="activity_actioncontainer">

            <div className="activity_inputcontainer6">

              {/* <AudioRecord /> */}

            </div>
            <div className="button_container">
              {submitted ?
                <button className="continue_button_Green">
                  <div onClick={() => {
                    // updateLSD(params.id)
                    // history.replace('/TaskScreen/' + params.id)
                  }}
                    className="button_text">Continue</div>
                </button>
                :
                <button className="continue_button_Orange">
                  <div onClick={() => {
                    // setsubmitted(!submitted)
                  }} className="button_text">SUBMIT</div>
                </button>
              }
            </div>
          </div>
        </div>
        :
        null
      }

      {(props.sl_misc === "SLIDER") ?
        <div className="activity_main_container">
          <div className="activity_container">
            <div className='PageTitle_OrangeU'>{props.sl_title}</div>
            <div className='pageDescription_Text'>{props.sl_body}</div>
          </div>

          <div className="activity_actioncontainer">

            <div className="activity_slider">
              <Slider
                defaultValue={0}
                step={null}
                marks={marks}
                value={inputvalue}
                onChange={(data, value) => {
                  console.log(value, data)
                  setinputvalue(value)
                }}
              />
            </div>
            <div className="button_container">
              {submitted ?
                <button className="continue_button_Green" onClick={() => {
                  //  history.replace('/TaskScreen/' + params.id) 
                }}>
                  <div className="button_text">Continue</div>
                </button>
                :
                <button className="continue_button_Orange"
                  onClick={() => {
                    // updateLSD(params.id)
                    // setsubmitted(true)
                    // saveDatatoLS(inputvalue)
                  }}>
                  <div className="button_text">SUBMIT</div>
                </button>
              }
            </div>
            <div className="popup_source" style={{ paddingTop: "2vh" }}>
              <a href='https://genomicgastronomy.com/work/2011-2/smog-tasting/'>source</a>
            </div>
          </div>

        </div>
        :
        null
      }

      {camera ?
        <>
          <div className='activity_scannerpopup_bg'>
            <div className='activity_scannerpopup_container'>

              <div className='activity_scannerpopup'>
                <div className='activity_Camersize' onClick={() => {
                  setshare(true)
                }}>
                  <div className='activity_Camera_div'>
                    <Camera
                      onTakePhotoAnimationDone={_handleTakePhotoAnimationDone}
                      // idealResolution={{ width: 500, height: 990 }}
                      idealFacingMode={idealFacingMode}
                      isMaxResolution={isMaxResolution}
                      isImageMirror={true}
                      isFullscreen={true}
                    />

                  </div>
                  <div className='activitycontainer'>
                    <div onClick={() => { setcamera(false) }}
                      className='activity_close_container'>
                      <img className='activity_close' src={close} />
                    </div>
                    <div className='activity_switchcamera-container'>
                      {idealFacingMode ?
                        <img onClick={(e) => {
                          setIdealFacingMode(!idealFacingMode);
                          // setIsMaxResolution(false);
                          console.log("front")
                        }} className='activity_switchcamera' src={switchcamera} />
                        :
                        <img onClick={(e) => {
                          setIdealFacingMode(FACING_MODES.ENVIRONMENT);
                          // setIsMaxResolution(false);
                          console.log("back")
                        }} className='activity_switchcamera' src={switchcamera} />
                      }
                    </div>
                  </div>
                </div>
              </div>

            </div>
          </div>
        </>
        :
        null
      }

      {popup ?
        <>
          <div className="quiz_popup_back">
            <div className="popup_container">
              <div className="popup_closecontainer">
                <img onClick={() => { setpopup(false) }} className='quiz_close' src={close} />
              </div>
              <div className="popup">

                <div className='PageTitle_OrangeU'>{`Thanks for the reply.`}
                </div>
                <div className='popup_Text'>{` Please find below our offer for your community.
               OFFER
                Enjoy FREE Yulu Rides For A Month!
                Use code NEW1500 & get FREE rides worth Rs.1,500.
                Enjoy 30% off each day for the next 30 days
`}
                </div>
                <div className='popup_Text'>{`Offer T&C 
Use Code NEW1500 and get Rs. 1500 upfront in your Yulu Money Account 
Coupon Validity - 30 Days 
Enjoy 30% discount on trips up to a max of Rs. 50/ day
Coupon valid for new users onlyg`}</div>


              </div>
              <div className='popup_continue_buttonContainer'>
                <button onClick={() => {


                }} className="continue_button_Orange">
                  <div className="button_text">Continue</div>
                </button>
              </div>
            </div>
          </div>
        </> : null
      }

      <div className="footer" />
    </div >

  )
}

export default ActivityScreen
